import React from "react";
import { Link } from "react-router-dom";
export const navbar = () => {
  return (
    <nav
      className="flex justify-between items-center h-16 bg-white text-black 
      relative shadow font-mono text-xl px-12"
      role="navigation" 
    >
      <Link to="/" className="pl-8 ">
        Karur Tech Solutions
      </Link>
      <div className="px-6 cursor-pointer md:hidden">
        <svg
          class="w-6 h-6"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M4 6h16M4 12h8m-8 6h16"
          ></path>
        </svg>
      </div>
      <div className="pr-8  md:block hidden  ">
        <Link className="p-4 hover:bg-yellow-300 py-2 rounded-full" to="/">
          Home
        </Link>
        <Link className="p-4 hover:bg-yellow-300 py-2 rounded-full" to="/Menu">
          Menu
        </Link>
        <Link className="p-4 hover:bg-yellow-300 py-2 rounded-full" to="/About">
          About
        </Link>
        <Link className="p-4 hover:bg-yellow-300 py-2 rounded-full" to="/Contact">
          Contact
        </Link>
      </div>
    </nav>
  );
};
export default navbar;
